// ---------- Markdown Default ----------

// @import url("https://unpkg.com/primer/build/build.css");

.highlight {
  width: 100%;
  overflow: auto;
  background: #fff;
}

.highlight .c {
  font-style: italic;
  color: #998;
}

/* Comment */

.highlight .err {
  color: #a61717;
  background-color: #e3d2d2;
}

/* Error */

.highlight .k {
  font-weight: 700;
}

/* Keyword */

.highlight .o {
  font-weight: 700;
}

/* Operator */

.highlight .cm {
  font-style: italic;
  color: #998;
}

/* Comment.Multiline */

.highlight .cp {
  font-weight: 700;
  color: #999;
}

/* Comment.Preproc */

.highlight .c1 {
  font-style: italic;
  color: #998;
}

/* Comment.Single */

.highlight .cs {
  font-style: italic;
  font-weight: 700;
  color: #999;
}

/* Comment.Special */

.highlight .gd {
  color: #000;
  background-color: #fdd;
}

/* Generic.Deleted */

.highlight .gd .x {
  color: #000;
  background-color: #faa;
}

/* Generic.Deleted.Specific */

.highlight .ge {
  font-style: italic;
}

/* Generic.Emph */

.highlight .gr {
  color: #a00;
}

/* Generic.Error */

.highlight .gh {
  color: #999;
}

/* Generic.Heading */

.highlight .gi {
  color: #000;
  background-color: #dfd;
}

/* Generic.Inserted */

.highlight .gi .x {
  color: #000;
  background-color: #afa;
}

/* Generic.Inserted.Specific */

.highlight .go {
  color: #888;
}

/* Generic.Output */

.highlight .gp {
  color: #555;
}

/* Generic.Prompt */

.highlight .gs {
  font-weight: 700;
}

/* Generic.Strong */

.highlight .gu {
  color: #aaa;
}

/* Generic.Subheading */

.highlight .gt {
  color: #a00;
}

/* Generic.Traceback */

.highlight .kc {
  font-weight: 700;
}

/* Keyword.Constant */

.highlight .kd {
  font-weight: 700;
}

/* Keyword.Declaration */

.highlight .kp {
  font-weight: 700;
}

/* Keyword.Pseudo */

.highlight .kr {
  font-weight: 700;
}

/* Keyword.Reserved */

.highlight .kt {
  font-weight: 700;
  color: #458;
}

/* Keyword.Type */

.highlight .m {
  color: #099;
}

/* Literal.Number */

.highlight .s {
  color: #d14;
}

/* Literal.String */

.highlight .na {
  color: #008080;
}

/* Name.Attribute */

.highlight .nb {
  color: #0086b3;
}

/* Name.Builtin */

.highlight .nc {
  font-weight: 700;
  color: #458;
}

/* Name.Class */

.highlight .no {
  color: #008080;
}

/* Name.Constant */

.highlight .ni {
  color: #800080;
}

/* Name.Entity */

.highlight .ne {
  font-weight: 700;
  color: #900;
}

/* Name.Exception */

.highlight .nf {
  font-weight: 700;
  color: #900;
}

/* Name.Function */

.highlight .nn {
  color: #555;
}

/* Name.Namespace */

.highlight .nt {
  color: #000080;
}

/* Name.Tag */

.highlight .nv {
  color: #008080;
}

/* Name.Variable */

.highlight .ow {
  font-weight: 700;
}

/* Operator.Word */

.highlight .w {
  color: #bbb;
}

/* Text.Whitespace */

.highlight .mf {
  color: #099;
}

/* Literal.Number.Float */

.highlight .mh {
  color: #099;
}

/* Literal.Number.Hex */

.highlight .mi {
  color: #099;
}

/* Literal.Number.Integer */

.highlight .mo {
  color: #099;
}

/* Literal.Number.Oct */

.highlight .sb {
  color: #d14;
}

/* Literal.String.Backtick */

.highlight .sc {
  color: #d14;
}

/* Literal.String.Char */

.highlight .sd {
  color: #d14;
}

/* Literal.String.Doc */

.highlight .s2 {
  color: #d14;
}

/* Literal.String.Double */

.highlight .se {
  color: #d14;
}

/* Literal.String.Escape */

.highlight .sh {
  color: #d14;
}

/* Literal.String.Heredoc */

.highlight .si {
  color: #d14;
}

/* Literal.String.Interpol */

.highlight .sx {
  color: #d14;
}

/* Literal.String.Other */

.highlight .sr {
  color: #009926;
}

/* Literal.String.Regex */

.highlight .s1 {
  color: #d14;
}

/* Literal.String.Single */

.highlight .ss {
