}

/* Name.Variable.Class */

.highlight .vg {
  color: #008080;
}

/* Name.Variable.Global */

.highlight .vi {
  color: #008080;
}

/* Name.Variable.Instance */

.highlight .il {
  color: #099;
}

/* Literal.Number.Integer.Long */

/* Make line numbers unselectable: excludes line numbers from copy-paste user ops */

.lineno::-moz-selection {
  background-color: transparent;
}

/* Mozilla specific */

.lineno::selection {
  background-color: transparent;
}

/* Other major browsers */

.highlight .lineno {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}

.article {
  h1,
  h2,
  h3,
  h4,
  .highlight {
    margin: 0.7em 0;
    text-align: left;
  }

  blockquote {
    padding-left: 16px;
    margin-bottom: 16px;
    color: #6a737d;
    border-left: 2px solid #959da5;
  }

  ul,
  ol {
    padding-left: 32px;
    margin-bottom: 16px;
  }

  li ul,
  li ol {
    padding-left: 16px;
    margin-bottom: 0;
  }
}
